import React, { useState, useEffect } from 'react';
import Header from './components/Header';
import ProductList from './components/ProductList';
//import FilterSidebar from './components/FilterSidebar';
//import FilterSidebar from './components/FilterSidebar';
import Popup from './components/popup';
import './App.css';
import { BrowserRouter, Route, Routes, useNavigate  } from 'react-router-dom';
import CategoryPage from './components/CategoryPage';

function AppContent() {
  const [products, setProducts] = useState([]);
  const [filters, setFilters] = useState({});
  const [searchTerm, setSearchTerm] = useState(''); //  state  
  const [isPopupOpen, setIsPopupOpen] = useState(false); // State  / popup
  const [isLoggedIn, setIsLoggedIn] = useState(false); //   ()
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();


  //  API  (  API)
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      try {
        const response = await fetch('http://localhost:5000/api/products');
        if (!response.ok) {
          throw new Error(`HTTP error! Status: ${response.status}`);
        }
        const data = await response.json();
        //     
        console.log(filters); //////// USELESSSSSS
        setProducts(shuffleArray(data)); // :  shuffledProducts
        //          
        if (!window.location.pathname.includes('/category')) {
          navigate('/', { replace: true });
        }
      } catch (error) {
        setError(error);
        console.error("Error fetching data:", error); // <----  console.error
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [navigate, filters]);

  //     ( -)
  const shuffleArray = (array) => {
    const newArray = [...array];
    for (let i = newArray.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [newArray[i], newArray[j]] = [newArray[j], newArray[i]];
    }
    return newArray;
  };

  const handleFilterChange = (newFilters) => {
    setFilters(newFilters);
    //      API
    //   products   
    //console.log('New filters:', newFilters);
  };

  const handleSearch = (term) => { //   
    setSearchTerm(term);
  };

  // const handleOpenPopup = () => { //     
  //   setIsPopupOpen(true);
  // };

  // const handleClosePopup = () => { //     
  //   setIsPopupOpen(false);
  // };

  // // New function to handle login from Popup
  // const handleLogin = () => {
  //   setIsLoggedIn(true);
  //   setIsPopupOpen(false);  // close the popup after successful login
  // };

  //   // New function to handle register from Popup (just close popup as example)
  // const handleRegister = () => {
  //   setIsLoggedIn(true);
  //   setIsPopupOpen(false);  // close the popup after successful register, also login as example
  // };

  // const handleCategorySelect = (category) => {
  //   navigate(`/category/${category}`);
  // };

const filteredProducts = products.filter(product => {
  return product.name.toLowerCase().includes(searchTerm.toLowerCase());
  // const searchTermLower = searchTerm.toLowerCase();
  // const productNameLower = product.name.toLowerCase();
  // const searchMatch = productNameLower.includes(searchTermLower); //   
  // // Category filter
  // if (selectedCategory) {
  //   return product.category === selectedCategory && searchMatch; // Search only within selected category
  // } else {
  //   return searchMatch; // Search across all products when no category is selected
  // }
  });

  if (loading) return <div>Loading...</div>;
  if (error)return <div>Error: {error.message}</div>;

  return (
    <div className="app-container">
      <Header
        onOpenPopup={() => setIsPopupOpen(true)}
        onSearch={handleSearch}
        onFilterChange={handleFilterChange}
        isLoggedIn={isLoggedIn}
      />
      <div className="main-content">
        <Routes>
          <Route path="/" element={            
            <ProductList products={filteredProducts} />
            } />
          <Route 
            path="/category/:categoryName" 
            element={
              <CategoryPage 
                products={products} 
                searchTerm={searchTerm} 
                loading={loading}
                error={error}
              />
            } 
          />
          {/* <Route path="/category/:categoryName" element={<CategoryPage />} /> */}
        </Routes>
      </div>

      {isPopupOpen && (
        <Popup
          isOpen={isPopupOpen}
          onClose={() => setIsPopupOpen(false)}
          onLogin={() => setIsLoggedIn(true)}
          onRegister={() => setIsLoggedIn(true)}
        />
      )}
    </div>
  );
}

function App() {
  return (
    <BrowserRouter>
      <AppContent />
    </BrowserRouter>
  );
}

export default App;