import React, { useState, useEffect } from 'react';
import './FilterSidebar.css';

const FilterSidebar = ({ onFilterChange }) => {
  const [priceRange, setPriceRange] = useState(100000);
  const maxPrice = 100000; // Maximum price value
  const [material, setMaterial] = useState('');
  const [color, setColor] = useState('');
  const [size, setSize] = useState('');
  const [availability, setAvailability] = useState('');
  const [brand, setBrand] = useState('');
  const [market, setMarket] = useState('');
  const [filtersList, setFiltersList] = useState({}); // : { material: ['', ''], color: [...] }

  useEffect(() => {
    fetch('/api/filters')
      .then(res => res.json())
      .then(data => {

        const grouped = {}; //   name
        data.forEach(f => {
          if (!grouped[f.name]) grouped[f.name] = [];
          grouped[f.name].push(f.display_name);
        });
        setFiltersList(grouped);
        
        //       
        // const filtersMap = {};
        // data.forEach(f => {
        //   filtersMap[f.name] = f.display_name;
        // });
        // //    ,  
        // //  ,     
        // if (filtersMap['material']) setMaterial(filtersMap['material']);
        // if (filtersMap['color']) setColor(filtersMap['color']);
        // if (filtersMap['size']) setSize(filtersMap['size']);
        // if (filtersMap['availability']) setAvailability(filtersMap['availability']);
        // if (filtersMap['brand']) setBrand(filtersMap['brand']);
        // if (filtersMap['market']) setMarket(filtersMap['market']);
      });
    //       
    setPriceRange(100000);
  }, []);

  //    
  const handleSaveFilters = async () => {
    const filters = [
      { name: 'material', displayName: '', value: material, productId: null },
      { name: 'color', displayName: '', value: color, productId: null },
      { name: 'size', displayName: '', value: size, productId: null },
      { name: 'availability', displayName: '', value: availability, productId: null },
      { name: 'brand', displayName: '', value: brand, productId: null },
      { name: 'market', displayName: '', value: market, productId: null },
      { name: 'price', displayName: '', value: priceRange.toString(), productId: null },
    ];

    //    
    for (const filter of filters) {
      await fetch('/api/filters/add', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(filter),
      });
    }
  };
  
  const handlePriceChange = (event) => {
    setPriceRange(parseInt(event.target.value, 10));
  };

  const handleMaterialChange = (e) => setMaterial(e.target.value);
  const handleColorChange = (e) => setColor(e.target.value);
  const handleSizeChange = (e) => setSize(e.target.value);
  const handleAvailabilityChange = (e) => setAvailability(e.target.value);
  const handleBrandChange = (e) => setBrand(e.target.value);
  const handleMarketChange = (e) => setMarket(e.target.value);

  const handleSubmit = (event) => {
    event.preventDefault();
    onFilterChange({
      price: priceRange,
      material,
      color,
      size,
      availability,
      brand,
      market,
    });
  };

  return (
    <div className="filter-sidebar">
      <h2></h2>
      <form onSubmit={handleSubmit}>
        <div classname="price-range-container">
          <label htmlFor="price"></label>
          <input
            type="range"
            id="price"
            min="0"
            max={maxPrice}
            value={priceRange}
            onChange={handlePriceChange}
            className="price-input"
          />
          <span className="price-value">  {priceRange.toLocaleString()} </span>
        </div>
        <div>
          <label htmlFor="material"></label>
          <select id="material" value={material} onChange={handleMaterialChange}>
            <option value=""></option>
            {/* <option value=""></option>
            <option value=""></option>
            <option value=""></option> */}
            {filtersList['material'] && filtersList['material'].map((val) => (
              <option key={val} value={val}>{val}</option>
            ))}
          </select>
        </div>
        <div>
        <label htmlFor="color"></label>
          <select id="color" value={color} onChange={handleColorChange}>
            <option value=""></option>
            {filtersList['color'] &&
              filtersList['color'].map((val) => (
                <option key={val} value={val}>{val}</option>
            ))}
            {/* <option value=""></option>
            <option value=""></option>
            <option value=""></option> */}
          </select>
        </div>
        <div>
        <label htmlFor="size"></label>
          <select id="size" value={size} onChange={handleSizeChange}>
            <option value=""></option>
            {filtersList['size'] &&
              filtersList['size'].map((val) => (
                <option key={val} value={val}>{val}</option>
            ))}
            {/* <option value=""></option>
            <option value=""></option>
            <option value=""></option> */}
          </select>
        </div>
        <div>
        <label htmlFor="availability"></label>
          <select id="availability" value={availability} onChange={handleAvailabilityChange}>
            <option value=""></option>
            {filtersList['availability'] &&
              filtersList['availability'].map((val) => (
                <option key={val} value={val}>{val}</option>
            ))}
            {/* <option value=""> </option>
            <option value="">  </option> */}
          </select>
        </div>
        <div>
        <label htmlFor="brand"></label>
          <select id="brand" value={brand} onChange={handleBrandChange}>
            <option value=""></option>
            {filtersList['brand'] &&
              filtersList['brand'].map((val) => (
                <option key={val} value={val}>{val}</option>
            ))}
            {/* <option value="gucci">gucci</option>
            <option value="prada">prada</option> */}
          </select>
        </div>
        <div>
        <label htmlFor="market"></label>
          <select id="market" value={market} onChange={handleMarketChange}>
            <option value=""></option>
            {filtersList['market'] &&
              filtersList['market'].map((val) => (
                <option key={val} value={val}>{val}</option>
            ))}
            {/* <option value="ozon">OZON</option>
            <option value="wb">Wildberries</option>
            <option value="yandex">.</option> */}
          </select>
        </div>
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default FilterSidebar;